export default function SelectJobType(props) {
    const classes = useStyles();
    const theme = useTheme();

    const onSelectJobType = (e, job_type) => {

        props.onSelectJobType(jobsTemplates[job_type]);
        props.onClose();
    }

    const handleClose = () => {
        //setSelectType(false);
        props.onClose();
    };   

    return (
        <React.Fragment>
            <Dialog fullScreen open={props.open} onClose={e => handleClose(e)} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={e => handleClose(e)} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="subtitle2" className={classes.title}>
                             
              </Typography>
                        <IconButton autoFocus color="inherit" disabled={true}>
                            <SaveIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                <Paper square className={classes.root}>
                    <Typography variant="h6">  </Typography>


                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                        >
                            <Typography variant="subtitle2">  </Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                                       ,      . 
             <br />       ,  , Standup
             </Box>
                            </Typography>

                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'standup')}>
                                
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel2a-content"
                            id="panel2a-header"
                        >
                            <Typography variant="subtitle2">   </Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                                           ,      . 
             <br />    ,      -
             </Box>
                            </Typography>
                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'update_request')}>
                                
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel3a-content"
                            id="panel3a-header"
                        >
                            <Typography variant="subtitle2">  ( )</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Typography component="div">
                                <Box fontSize="fontSize" m={1}>
                                           .       
             <br />      ,    
             </Box>
                            </Typography>
                        </ExpansionPanelDetails>
                        <ExpansionPanelActions>
                            <Button color="primary" onClick={e => onSelectJobType(e, 'send_report')}>
                                
            </Button>
                        </ExpansionPanelActions>
                    </ExpansionPanel>
                </Paper>
            </Dialog>
        </React.Fragment>
    );
  }

