  function EditQueryDialog(props) {
    const [open, setOpen] = React.useState(false);
    const [query, setQuery] = React.useState('');
  
    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };
  
    const handleSave = () => {
      if(query && query.length>0 && query!=props.query){
        //props.onRespondentChangeName(props.respondent.id, newName);
      }    
      handleClose();
    };
  
    const  onChangeQuery = (query) =>{
        const q = formatQuery(query, 'sql')
        console.log(q);
        setQuery(q);
    }
    
    const fields = [
        { name: 'firstName', label: 'First Name' },
        { name: 'lastName', label: 'Last Name' },
        { name: 'age', label: 'Age' },
        { name: 'address', label: 'Address' },
        { name: 'phone', label: 'Phone' },
        { name: 'email', label: 'Email' },
        { name: 'twitter', label: 'Twitter' },
        { name: 'isDev', label: 'Is a Developer?', value: false }
      ];

    return (
      <ListItemSecondaryAction >
        <IconButton edge="end" aria-label="menu" aria-controls="job-item-menu" aria-haspopup="true" onClick={handleClickOpen}>
          <EditIcon />
        </IconButton>      
        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
          <DialogContent>
            <DialogContentText>
               :
            </DialogContentText>
            <QueryBuilder fields={fields} onQueryChange={onChangeQuery} />;
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              
            </Button>        
            <Button onClick={handleSave} color="primary">
              
            </Button>
          </DialogActions>
        </Dialog>
      </ListItemSecondaryAction>
    );
  }

function QuestionEditor(props){
    const classes = useStyles();
    const onChangeQuestionText = (event) => {
        const v = event.target.value;
        props.onChangeQuestionText(props.idx, v);
    }
    
    const onChangeQuestionAnswerType = (event) => {
        const v = event.target.value;
        props.onChangeQuestionAnswerType(props.idx, v);
    }
    
    const onDeleteQuestion = (event) => {
        const v = event.target.value;
        props.onDeleteQuestion(props.idx);
    }
    
    const onAddQuestion = (event) => {
        const v = event.target.value;
        props.onAddQuestion(props.idx);
    }
    
    const onChangeJobLogColumn = (event) => {
        const v = event.target.value;
        props.onChangeJobLogColumn(props.idx, v);
    }
    

    const handleAddAnswerChoice = (chip) => {
        props.handleAddAnswerChoice(props.idx, chip);
    }  
    
    const handleDeleteAnswerChoice = (chip, index) => {
        props.handleDeleteAnswerChoice(props.idx, chip, index);
    }     

    const onChangeQuestionCondition = (event) => {
        const v = event.target.value;
        props.onChangeQuestionCondition(props.idx, v);
    }     
    
    
    return(
        <React.Fragment >
            <Paper className={classes.question_list}>
            <Typography variant="h6">
            {" "+(props.idx+1)}
          </Typography>                
                <TextField
                    id={"job-question-text"+props.idx}

                    value={props.question.question_text}
                    onChange={onChangeQuestionText}                    
                    placeholder="  "
                    helperText=""
                    fullWidth
                    multiline
                    margin="normal"
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
                        <TextField
                            id={"job-question-answer_type"+props.idx}
                            label=" "
                            value={props.question.answer_type?props.question.answer_type:""}
                            onChange={onChangeQuestionAnswerType}
                            placeholder=" "
                            helperText=""
                            fullWidth
                            select
                            margin="normal"
                            className={classes.selectEmpty}
                            InputLabelProps={{
                                shrink: true,
                            }}>
                            <MenuItem value="">
                                <em>None</em>
                            </MenuItem>                                
                            <MenuItem key="text" value="text">
                                
                            </MenuItem>
                            <MenuItem key="number" value="number">
                                
                            </MenuItem>
                            <MenuItem key="percent" value="percent">
                                 (0  100)
                            </MenuItem>
                            <MenuItem key="date" value="date">
                                
                            </MenuItem>
                            <MenuItem key="due" value="due">
                                 ( )
                            </MenuItem>
                            <MenuItem key="choice" value="choice">
                                
                            </MenuItem>
                            <MenuItem key="nothing" value="nothing">
                                 
                            </MenuItem>
                        </TextField> 
                        <div hidden={props.question.answer_type!="choice"}> 
                        <ChipInput
                            id={"job-question-answer_choice"+props.idx}
                            label=" "
                            value={props.question.answer_choice}
                            onAdd={(chip) => handleAddAnswerChoice(chip)}
                            onDelete={(chip, index) => handleDeleteAnswerChoice(chip, index)}
                        />       
                        </div> 
                        <TextField
                            id="job-log_column"
                            label="   "
                            value={props.question.log_column?props.question.log_column.value:''}
                            onChange={onChangeJobLogColumn}
                            placeholder=""
                            helperText=""
                            fullWidth
                            select
                            margin="normal"
                            className={classes.selectEmpty}
                            InputLabelProps={{
                                shrink: true,
                            }}>
                            <MenuItem value="">
                                <em>None</em>
                            </MenuItem>                                
                            {props.sheet_columns && props.sheet_columns.map((option) => (
                                <MenuItem key={option.value} value={option.value}>
                                  {option.label}
                                </MenuItem>
                              ))}
                        </TextField>    
                        <TextField
                            id={"job-question-condition"+props.idx}
                    label="  "
                    value={props.question.condition}
                    onChange={onChangeQuestionCondition}                    
                    placeholder="   "
                    helperText=""
                    fullWidth
                    multiline
                    margin="normal"
                />

                        <Grid container justify="flex-end" alignItems="flex-end">
                        <IconButton color="secondary" aria-label="add" className={classes.margin} onClick={onAddQuestion}>
                            <AddIcon fontSize="small" />
                        </IconButton>
                        <IconButton aria-label="delete" className={classes.margin} onClick={onDeleteQuestion}>
                            <DeleteIcon fontSize="small" />
                        </IconButton>
                        </Grid>
            </Paper>
        </React.Fragment>        
    )
}

  function TabPanel(props) {
    const { children, value, index, ...other } = props;
    const classes = useStyles();
    return (
      <div
        role="tabpanel"      
        hidden={value !== index}
        id={`scrollable-prevent-tabpanel-${index}`}
        aria-labelledby={`scrollable-prevent-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Paper className={classes.paper}>
            <Typography>{children}</Typography>
          </Paper>
        )}
      </div>
    );
  }

